import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import * as level1DataProp from '../../propTypes/level1Data';
import APLabelWithText from '../common/APLabelWithText/APLabelWithText';
import APIcon from '../common/APIcon/APIcon';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import {
  formatPercentageValue,
  formatNumberToLocale
} from '../../helpers/numberFormatter';
import { calculateRolling24HrPxChangeValue } from '../../helpers/level1Helper';

import './MarketDataCardComponent.css';

const marketDataItemClasses = getBEMClasses('market-data-card');

const MarketDataCardComponent = (props, context) => {
  const {
    product1,
    product2,
    level1Data: {
      LastTradedPx,
      Rolling24HrVolume,
      Rolling24HrPxChange,
      SessionHigh,
      SessionLow,
      BestBid,
      BestOffer
    },
    coin
  } = props;
  const rolling24HrPxChangeValue = calculateRolling24HrPxChangeValue(
    LastTradedPx,
    Rolling24HrPxChange
  );

  return (
    <div className={marketDataItemClasses()}>
      <div className={marketDataItemClasses('row')}>
        {coin ? (
          <React.Fragment>
            <ProductIconContainer
              iconFileName={product1.iconFileName}
              size={40}
            />
            <div className={marketDataItemClasses('coins-header-text')}>
              {`${product1.ProductFullName} ${product1.ProductSymbol}`}
            </div>
          </React.Fragment>
        ) : (
          <APLabelWithText
            label={`${product1.ProductFullName} / ${product2.ProductFullName}`}
            text={`${product1.ProductSymbol}/${product2.ProductSymbol}`}
            customClass={marketDataItemClasses()}
            classModifiers={'header'}
          />
        )}
      </div>
      <div className={marketDataItemClasses('row')}>
        <APLabelWithText
          label={context.t('Last Price')}
          text={formatNumberToLocale(LastTradedPx, product2.ProductSymbol)}
          customClass={marketDataItemClasses()}
        />
        <APLabelWithText
          label={context.t('24-Hour Change')}
          text={formatNumberToLocale(
            rolling24HrPxChangeValue,
            product2.ProductSymbol
          )}
          customClass={marketDataItemClasses()}
          classModifiers={
            rolling24HrPxChangeValue.gte(0) ? '24h-positive' : '24h-negative'
          }
        />
      </div>
      <div className={marketDataItemClasses('row')}>
        <APLabelWithText
          label={context.t('24-Hour Pct Change')}
          text={formatPercentageValue(Rolling24HrPxChange)}
          customClass={marketDataItemClasses()}
          classModifiers={
            Rolling24HrPxChange >= 0 ? '24h-positive' : '24h-negative'
          }
        />
        <APLabelWithText
          label={context.t('24-Hour Vol.')}
          text={formatNumberToLocale(Rolling24HrVolume, product1.ProductSymbol)}
          customClass={marketDataItemClasses()}
        />
      </div>
      <div className={marketDataItemClasses('row')}>
        <APLabelWithText
          label={context.t('24-Hour High')}
          text={formatNumberToLocale(SessionHigh, product2.ProductSymbol)}
          customClass={marketDataItemClasses()}
        />
        <APLabelWithText
          label={context.t('24-Hour Low')}
          text={formatNumberToLocale(SessionLow, product2.ProductSymbol)}
          customClass={marketDataItemClasses()}
        />
      </div>
      <div className={marketDataItemClasses('row')}>
        <APLabelWithText
          label={context.t('Bid')}
          text={formatNumberToLocale(BestBid, product2.ProductSymbol)}
          customClass={marketDataItemClasses()}
          classModifiers="bid"
        />
        <APLabelWithText
          label={context.t('Ask')}
          text={formatNumberToLocale(BestOffer, product2.ProductSymbol)}
          customClass={marketDataItemClasses()}
          classModifiers="ask"
        />
      </div>
      {coin && (
        <div className={marketDataItemClasses('row')}>
          <div className={marketDataItemClasses('activity')} onClick={() => {}}>
            View Activity &#8594;
          </div>
          <div className={marketDataItemClasses('icons')}>
            <div
              className={marketDataItemClasses('icon-container')}
              onClick={() => {}}>
              <APIcon name="buy" customClass={marketDataItemClasses('icon')} />
            </div>
            <div
              className={marketDataItemClasses('icon-container')}
              onClick={() => {}}>
              <APIcon name="sell" customClass={marketDataItemClasses('icon')} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

MarketDataCardComponent.propTypes = {
  level1Data: level1DataProp.type.isRequired,
  product1: PropTypes.object.isRequired,
  product2: PropTypes.object.isRequired,
  coin: PropTypes.bool
};

MarketDataCardComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default MarketDataCardComponent;



