import BigNumber from 'bignumber.js';

export const calculateRolling24HrPxChangeValue = (price, change) => {
  // rolling24HrPxChangeValue = LastTradedPx*(Rolling24HrPxChange(%)/(1 + Rolling24HrPxChange(%)))
  price = new BigNumber(price);
  change = new BigNumber(change / 100);
  return price.times(change.div(change.plus(1)));
};



