import React from 'react';
import MarketDataCardComponent from './MarketDataCardComponent';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './MarketDataCardsComponent.css';

const bemClasses = getBEMClasses('market-data-cards');

const MarketDataCardsComponent = props => {
  const { level1, instruments, products } = props;

  const marketDataCards = instruments.map(instrument => {
    const { InstrumentId } = instrument;
    const level1Data = level1[InstrumentId];

    if (!level1Data) return null;

    const { Product1, Product2 } = instrument;
    const product1 = products.find(x => x.ProductId === Product1);
    const product2 = products.find(x => x.ProductId === Product2);

    return (
      <div
        key={InstrumentId}
        className={`${bemClasses('card')} container-with-shadow`}>
        <MarketDataCardComponent
          product1={product1}
          product2={product2}
          level1Data={level1Data}
        />
      </div>
    );
  });

  return (
    <div className={bemClasses()}>
      <div className={bemClasses('row')}>{marketDataCards}</div>
    </div>
  );
};

export default MarketDataCardsComponent;



