import { connect } from 'react-redux';
import MarketDataCardsComponent from './MarketDataCardsComponent';
import { productPositionAssetsSelector } from '../../redux/selectors/productPositionSelectors';

const mapStateToProps = state => {
  const { level1 } = state;
  const { instruments } = state.instrument;
  const products = productPositionAssetsSelector(state);

  return { level1, instruments, products };
};

export default connect(mapStateToProps)(MarketDataCardsComponent);



